body {
    background-color: #00B8FF;
    margin: 0 !important;
    padding: 0 !important;
    color: white;
}

.topnav {
    overflow: hidden;
    font-family: 'Oswald';
    position: fixed;
    width: 100%;
    list-style: none;
    text-align: right;
}

.logo {
    float: left;
}

.menu {
    display: inline-block;
    margin-top: 22px;
}

.topnav a {
    padding: 20px;
    text-decoration: none;
    color: #ffffff;
}

#icon {
    display: none;
}

.content {
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 100px;
}

/* Extra small devices (phones, 600px and down) */

@media only screen and (max-width: 600px) {
    .topnav {
        background-color: rgba(62, 62, 62, 1);
        font-size: 125%;
    }
    .logo { 
        float:left;
    }
    .topnav img {
        width: 166.6px;
        height: 50px;
        margin-top: 5px;
    }
    .menu {
        display: none;
    }
    .dropdown {
        display: inline-block;
        margin-top: 0%;
        border: 1px solid;
        position: relative;
    }
    #icon {
        cursor: pointer;
        margin-bottom: 5px;
        padding: 10px;
        padding-right: 20px;
        display: inline-block;
        font-size: 120%;
    }
    #icon:hover {
        color: #f2f2f2;
    }
    .dropmenu {
        display: block;
    }
    .dropmenu a {
        display: block;
        padding: 10px;
        padding-left: 20px;
        padding-right: 20px;
    }
    .dropmenu a:hover {
        color: #f2f2f2;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */

@media only screen and (min-width: 600px) {
    .topnav {
        background-color: rgba(62, 62, 62, 1);
        font-size: 125%;
    }
    .menu {
        display: inline-block;
        margin-right: 5%;
    }
    .logo {
        margin-left: 5%;
        padding-top: 5px;
    }
    .topnav img {
        width: 208.3px;
        height: 62.5px;
    }
    .topnav a:hover {
        color: #00B8FF;
    }
    #icon {
        display: none;
    }
    .dropdown {
        display: none;
    }
}

/* Large devices (laptops/desktops, 992px and up) */

@media only screen and (min-width: 992px) {
    .topnav {
        font-size: 150%;
        background-color: rgba(62, 62, 62, 0.6);
    }
    .logo {
        margin-left: 15%;
    }
    .topnav img {
        width: 250px;
        height: 75px;
    }
    .menu {
        display: inline-block;
        margin-right: 15%;
    }
    .topnav a:hover {
        color: #3d3d3d;
    }
    .dropdown {
        display: none;
    }
}


